.table thead th {
  background-color: var(--table-head-bg, #fafafa);
  /* border-bottom: 1px solid var(--divider-color); */
  padding: 16px;
  font-weight: 500;
  color: var(--text-secondary);
  text-transform: uppercase;
  letter-spacing: 0.5px;
  font-size: 0.75rem;
}

.table tbody td {
    padding: 16px;
    /* border-bottom: 1px solid var(--divider-color); */
    vertical-align: middle;
}

.btn-icon {
    width: 36px;
    height: 36px;
    padding: 0;
    border-radius: 50%;
    display: flex
;
    align-items: center;
    justify-content: center;
}

/* Hover for table rows to respect color theme */
.table-hover tbody tr:hover {
  background-color: var(--table-row-hover, rgba(0,0,0,0.04));
}
